import Signal from 'JS/Signal';
import normalizeWheel from 'normalize-wheel';

export class EventHandler {
	constructor() {
		this.initEvent();
		this.time = +new Date();
		this.lastTime = null;
	}

	initEvent() {
		this.onMouseMoveEvent = new Signal();
		this.onScrollEvent = new Signal();
		this.onClickEvent = new Signal();
		document.body.addEventListener('mousemove', this.onMouseMove.bind(this), { passive: true });
		document.body.addEventListener('wheel', this.onScroll.bind(this), { passive: false });
		// document.body.addEventListener('touchmove', this.onTouchMove.bind(this), {passive: false});
		document.body.addEventListener('click', this.onClick.bind(this));
	}

	onClick(event) {
		this.onClickEvent.dispatch(event);
	}

	onMouseMove(event) {
		this.onMouseMoveEvent.dispatch(event);
	}

	onScroll(event) {
		// 
		event.preventDefault();
		this.time = +new Date();
		// 
		// Todo:Windows
		if(this.time - this.lastTime > 1400 || !this.lastTime){
			let normalEvent = normalizeWheel(event);
			this.onScrollEvent.dispatch(normalEvent);
			this.lastTime = this.time;
		}
	}

	onTouchMove(event){
		// 
		event.preventDefault();
	}
}
