export default "precision highp float;\n#define GLSLIFY 1\nvarying vec2 vUv;\nvarying float vPreAlpha;\nuniform vec3 color;\nuniform float type;\n\n// https://www.shadertoy.com/view/llVyWW\nfloat sdPentagon(vec2 p, float r){\n    const vec3 k = vec3(0.809016994,0.587785252,0.726542528); // pi/5: cos, sin, tan\n    p.y = -p.y;\n    p.x = abs(p.x);\n    p -= 2.0*min(dot(vec2(-k.x,k.y),p),0.0)*vec2(-k.x,k.y);\n    p -= 2.0*min(dot(vec2( k.x,k.y),p),0.0)*vec2( k.x,k.y);\n\tp -= vec2(clamp(p.x,-r*k.z,r*k.z),r);    \n    return length(p)*sign(p.y);\n}\n\n// Heart curve suggested by IQ, improved by Dave_Hoskins.\nfloat heart(vec2 p) {\n    // Center it more, vertically:\n    p.y += .6;\n    // This offset reduces artifacts on the center vertical axis.\n    const float offset = .3;\n    // (x^2+(1.2*y-sqrt(abs(x)))^21)\n    float k = 1.2 * p.y - sqrt(abs(p.x) + offset);\n    return p.x * p.x + k * k - 1.;\n}\n\n// signed distance to an equilateral triangle\nfloat sdEquilateralTriangle(in vec2 p) {\n    const float k = sqrt(3.0);\n    p.x = abs(p.x) - 1.0;\n    p.y = p.y + 1.0/k;\n    if( p.x+k*p.y>0.0 ) p=vec2(p.x-k*p.y,-k*p.x-p.y)/2.0;\n    p.x -= clamp( p.x, -2.0, 0.0 );\n    return -length(p)*sign(p.y);\n}\n\nfloat sdCircle(in vec2 p, in float r) {\n    return length(p)-r;\n}\n\nfloat sdHexagram( in vec2 p, in float r ){\n    const vec4 k = vec4(-0.5,0.86602540378,0.57735026919,1.73205080757);\n    \n    p = abs(p);\n    p -= 2.0*min(dot(k.xy,p),0.0)*k.xy;\n    p -= 2.0*min(dot(k.yx,p),0.0)*k.yx;\n    p -= vec2(clamp(p.x,r*k.z,r*k.w),r);\n    return length(p)*sign(p.y);\n}\n\nvoid main() {\n\tvec2 st = (vUv * 2.0 - 1.0); // [-1, 1]\n    float r;\n\n    if(type>1.1)\n        r = sdCircle(st, .5);\n    else if(type>0.1)\n        r = heart(st);\n    else\n        r = sdPentagon(st, 0.5);\n    \n\tfloat alpha = 1. - sign(r);\n\n\tvec3 col = color;\n\n\tgl_FragColor = vec4(col, alpha * vPreAlpha);\n}";