export default "precision highp float;\n#define GLSLIFY 1\nuniform sampler2D map;\nvarying vec2 vUv;\nuniform float progress;\nuniform vec2 resolution;\nuniform bool isNext;\n\nvoid main() {\n    vec2 uv = vUv;\n    vec4 color = texture2D(map, uv);\n    \n    float y = gl_FragCoord.y / resolution.y; // [0, 1]\n\n    // float x = gl_FragCoord.x / resolution.x; // [-0.5, 0.5]\n    // float edge = x * edgeProgress + progress;\n    \n    float opacity = step(progress, y);\n    if(isNext){\n        opacity = 1. - opacity;\n    }\n    gl_FragColor = vec4(\n        vec3( ( color.rgb ) / color.a ),\n        opacity\n    );\n    // Todo: Test scissor\n    // progresssccisor\n    // gl_FragColor = vec4(color);\n}\n";